import axios from 'axios';
export default {
  name: 'Questionare',
  props: ['action'],
  data() {
    return {
      pending: false
    };
  },
  components: {},
  directives: {},
  computed: {
    extra() {
      return this.action.extra;
    },
    setting () {
      const { getClientApiConfig } = this.$store.state.config;
      const { endpoint, method, data } = this.extra;
      const { headers, url } = getClientApiConfig();
      return {
        headers,
        method,
        url: url && url + endpoint,
        data,
      }
    }
  },
  methods: {
    async send() {
      this.$emit('outputScrollToBottom');
      if (this.pending || !this.setting.url) { return; }
      this.pending = true;
      await axios(this.setting);
      this.pending = false;
    }
  }
};



